
let raw = `

            # 王震的述职报告

            姓名：王震

            年龄：23岁

            所在岗位：前端开发工程师

            ## 工作经历

            下面是我的工作经历

            ### 2018-2019年

            绿城建筑科技集团品牌管理高级专员

        `
let html = ``

//检测是否是主页面
const isMain = str => (/^#{1,2}(?!#)/).test(str);
//检测是否是子页面
const isSub = str => (/^#{3}(?!#)/).test(str);

let slides = document.getElementsByClassName('slides')[0];
let settings = document.getElementsByClassName('icon')[0]
let menu = document.getElementsByClassName('menu')[0]

settings.onclick = function(){

}

function convertRaw(raw) {
    //使用正则表达式将每一个单独的页面放入数组
    let arr = raw.split(/\n(?=\s*#)/).filter(s => s != '').map(s => s.trim())
    for (let i = 0; i < arr.length; i++) {
        if (arr[i + 1] !== undefined) {
            if (isMain(arr[i]) && isMain(arr[i + 1])) {
                html += `
                    <section data-markdown>
                        <textarea data-template>
                            ${arr[i]}
                        </textarea>
                    </section>
                `
            } else if (isMain(arr[i]) && isSub(arr[i + 1])) {
                html += `
                    <section>
                    <section data-markdown>
                        <textarea data-template>
                            ${arr[i]}
                        </textarea>
                    </section>
                `
            } else if (isSub([arr[i]]) && isSub([arr[i + 1]])) {
                html += `
                    <section data-markdown>
                        <textarea data-template>
                            ${arr[i]}
                        </textarea>
                    </section>
                `
            } else if (isSub([arr[i]]) && isMain(arr[i + 1])) {
                html += `
                    <section data-markdown>
                        <textarea data-template>
                            ${arr[i]}
                        </textarea>
                    </section>
                    </section>
                `
            }
        } else {
            if (isMain([i])) {
                html += `
                    <section data-markdown>
                        <textarea data-template>
                            ${arr[i]}
                        </textarea>
                    </section>
                `
            } else if (isSub(arr[i])) {
                html += `
                    <section data-markdown>
                        <textarea data-template>
                            ${arr[i]}
                        </textarea>
                    </section>
                    </section>
                `
            }
        }
    }
    return html
}

function loadMarkdown(raw){
    localStorage.markdown= raw
    location.reload()
}

function start(){
    let TPL ='# One Slide'
    let html = convertRaw(raw|| TPL)
    slides.innerHTML = html
    // More info https://github.com/hakimel/reveal.js#configuration
    Reveal.initialize({
        controls: true,
        progress: true,
        center: true,
        hash: true,

        transition: 'slide', // none/fade/slide/convex/concave/zoom

        // More info https://github.com/hakimel/reveal.js#dependencies
        dependencies: [
            {
                src: 'plugin/markdown/marked.js', condition: function () {
                    return !!document.querySelector('[data-markdown]');
                }
            },
            {
                src: 'plugin/markdown/markdown.js', condition: function () {
                    return !!document.querySelector('[data-markdown]');
                }
            },
            {src: 'plugin/highlight/highlight.js'},
            {src: 'plugin/search/search.js', async: true},
            {src: 'plugin/zoom-js/zoom.js', async: true},
            {src: 'plugin/notes/notes.js', async: true}
        ]
    });
}

start()


